import { sendSave } from './fetchdata'
const row2key = ['current_dome_finish','current_overseas_finish']
const row1key = ["global_production","global_outbound",  "dome_outbound","overseas_outbound",  "global_active", "dome_active", "overseas_active","global_signed","dome_signed","overseas_signed","season_outbound"]
const thead = [
    ['手机2月简报'],
    ['标题','数量（万台）','环比增长率','同比增长率'],
    ['标题','百分比']
]
const title = ['全球生产量','全球出库量','国内出库量','海外出库量','全球激活量','国内激活量','海外激活量','全球妥投量','国内妥投量','海外妥投量','Q1出库量']

const mykey = [
    ['标题'],
    ['标题','num','ring_rate','equal_rate'],
    ['标题','num']
]
const origindata={
    current_dome_finish:{num:0},
    current_overseas_finish:{num:0}
}
export function table(simpleData){
    let data = simpleData.actual_data
    if(!data){
        simpleData.actual_data = origindata
        data = simpleData.actual_data
    }
    const status = simpleData.status
    const maintain = []
    let i = 0
    for(let key in data){
        if(key==='current_dome_finish'||key==='current_overseas_finish'){
            continue
        }
        maintain.push(
            [title[i] , data[key].num, data[key].ring_rate , data[key].equal_rate ]
        )
        i++
    }
    console.log(maintain,'maintian')
    const content = [
        [
            ['数据抓取时间 03月01日 00:00，可能存在1%以内的误差']
        ],
        maintain,
        [
            ['当月国内销售达成',data.current_dome_finish&&data.current_dome_finish.num],
            ['当月海外销售达成',data.current_overseas_finish&&data.current_overseas_finish.num]
        ]
    
    ]
    const table = {
        origin : simpleData,
        content,
        thead,
        mykey,
        status,
        submitChange(row,index,mykey,newData,id){
            console.log(row,index,mykey,newData,id,simpleData)
            if(id==1){
                let type = row1key[row]
                table.origin.actual_data[type][mykey] = newData
                console.log(type)
            }else if(id==2){
                let type = row2key[row]
                table.origin.actual_data[type][mykey] = newData
            }
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data
            }
            sendSave(data,func)
        }
    }
    return table

}
export default table


