import React, {Component } from 'react'
import styles from './index.scss'
import { Icon } from 'antd'
import Column from 'antd/lib/table/Column';
import shape2 from './img/Shape-2.png'
let height=0,width = 0
class Image extends Component {
    constructor(props){
        super(props)
        this.state = {
            plus: 1,
            showbtn: true
        }
    }
    handleDrag(e){
        let prex = e.clientX,
            prey = e.clientY
        this.el.onmousemove = e =>{
            let left = parseInt(this.el.style.left),
                top = parseInt(this.el.style.top)
            let container = e.target.parentNode
            
            let xx = prex - e.clientX,
                yy = prey - e.clientY
            prex = e.clientX
            prey = e.clientY

            container.scrollLeft +=  xx
            container.scrollTop += yy
        }
    }
    handleLeave(){
        this.el.onmousemove = null
    }
    handleScale(e){
        let plus = this.state.plus, i = 0, toPlus = 0
        let prevZoom = this.el.style.zoom
        if(e.target.innerText === '-' && plus>0.3){
              toPlus = 0.2
//            plus = plus-0.2
        }
        if(e.target.innerText === '+' && plus<1.9){
            toPlus = -0.2
//            plus = plus+0.2
        }
        console.log(this.el)
        let time = setInterval(()=>{
            i++
            this.el.style.zoom -= (toPlus/30)
            console.log(this.el.style.zoom-toPlus/30)
            if(i === 30){
                clearInterval(time)
                this.setState({
                    plus:plus - toPlus
                })
                return
            }
        },1)
//        this.el.style.zoom = plus
    }
    handleToggleBtn(callback){
        this.setState({
            showbtn:true
        },()=>{
            callback && callback()
        })
    }
    handleOut(time){
        this.t = setTimeout(()=>{
            this.setState({
                showbtn:false
            })
        },time||1500)
    }
    toggle(e){
        if(e.target.dataset.name==='close'){
            if(!this.state.showbtn){
                clearTimeout(this.t)
            }
            this.setState({
                showbtn: true,
                plus: 1
            },()=>
            this.props.toggle(e))
        }
    }
    close(){
        this.props.toggle()
    }
    componentWi(){
        console.log('will')
    }
    componentWillReceiveProps(nextProps){
        if(nextProps.show){
        }
        this.handleToggleBtn(()=>{
            setTimeout(() => {
                this.setState({
                    showbtn:false
                })
            }, 3000);
        })
        
    }

    render(){
        if(!this.props.show) return<div></div>
        const closeStyle = {
            position: 'fixed',
            right: '2vw',
            top: '2vh',
            color: 'white',
            fontSize : 50,
        }
        
        const { showbtn } = this.state
        console.log(showbtn)
        const {src, type, show} = this.props
        return (
            <div style={{display:show?'':'none'}}>
                <div className={styles["modal-mask"]} >
                </div>
                <div data-name="close" className={styles["modal-wrap"]} onClick={(e)=>{this.toggle(e)}}>
                    <div data-name="close" className={styles["modal-content"]}>
                        {type==='img'
                        ?(<img style={{zoom:1}} draggable="false" ref={el=>this.el = el} onMouseDown={(e)=>this.handleDrag(e)} onMouseUp={e=>{this.handleLeave()}} onMouseLeave={e=>this.handleLeave()} className={styles[""]} src={src} />)
                        :
                        (<div className={styles.scrollbar} style={{zoom:1}} draggable="false" ref={el=>this.el = el} style={{
                                      color:'#111',
                                      width:'90vw',
                                      height:'90vh',
                                      backgroundColor:'rgba(240,240,240,.7)',
                                      fontSize:'25px',
                                      overflow:'auto',
                                      flexDirection:'column',}}>
                          {src}
                        </div>)//<iframe style={{zoom:1,backgroundColor:'white',minWidth:'80vw',minHeight:'80vh'}} ref={el=>this.el = el} src=""></iframe>)
                        }
                        <div ref={div=>this.div = div} style={{ display:'none',opacity:showbtn?'1':'0'}} className={styles["modal-scale"]} onClick={(e)=>this.handleScale(e)} onMouseOver={()=>this.handleToggleBtn()} onMouseOut={()=>{this.handleOut()}}>
                            <button>-</button>
                            <button>+</button>
                        <span className={styles["modal-plus"]}>{Math.floor(this.state.plus*100)}%</span>
                        </div>
                        <Icon data-name="close" style={closeStyle} type="close" />
                    </div>
                </div>
            </div>
        )
    }
}

export default Image


