import { sendSave } from './fetchdata'
const thead = [
    ['日期','全球','国内','海外'],
    ['坐标轴','最小值','最大值','间隔单位'],
    ['颜色','1','2','3']
]
const mykey = [
    ['日期','global_active','dome_active','overseas_active'],
    ['','min','max','gap'],
    ['','color1','color2','color3']
]
let axis = [
    ['左（主要）','','','']
]
const colors = [
    ['注释内容','','','']
]
const origindata = {
    global_active:{},
    dome_active:{},
    overseas_active:{}
}
export function table2(data){
    console.log(data)
    let { actual_data, status, config_data } = data.data.global_month_active
    if(!actual_data){
        actual_data = {}
        data.data.global_month_active.actual_data = actual_data
        actual_data.detail = origindata
    }
    const detail = actual_data.detail
    const maintain = []
    if(config_data){
        axis = JSON.parse(window.HTMLDecode(config_data)).axis
    }
    for(let key in detail.global_active){
        maintain.push(
            [ key, detail.global_active[key] , detail.dome_active[key], detail.overseas_active[key]]
        )
    }
    const content = [
            maintain
        ,
            axis
        ,
            colors
    ]
console.log(content,'contetn')
    const table2 = {
        thead: thead,
        content: content,
        origin: data.data.global_month_active,
        mykey:mykey,
        status,
        submitChange(row,index,mykey,newData){
            let date = Number(row) + 1
            if(mykey=='日期'){
                return
            }
            if(['min','max','gap'].indexOf(mykey)>-1){
                mykey==='min'&&(content[1][0][1] = newData)
                mykey==='max'&&(content[1][0][2] = newData)
                mykey==='gap'&&(content[1][0][3] = newData)
                return
            }
            if(['color1','color2','color3'].indexOf(mykey)>-1){
                mykey==='color1'&&(content[2][0][1] = newData)
                mykey==='color2'&&(content[2][0][2] = newData)
                mykey==='color3'&&(content[2][0][3] = newData)
                return
            }
            table2.origin.actual_data.detail[mykey][date] = newData
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table2.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data,
                config_data: {
                    axis,
                    colors,
                }
            }
            sendSave(data,func)
        }
    }
    return table2

}
export default table2


