import { sendSave } from './fetchdata'

const thead = ['2018国内汇总','生产','出库','激活']
export function table10(data){
    const dome_season_total_data = data.data.dome_season_total_data
    let { actual_data, status } = dome_season_total_data
    if(!actual_data){
        data.data.dome_season_total_data.actual_data = {}
        actual_data = data.data.dome_season_total_data.actual_data
    }
    const content = [
        [ actual_data.type,actual_data.production, actual_data.outbound, actual_data.active_day]
    ]
    const table10 = {
        origin: dome_season_total_data,
        thead,
        content,
        status,
        mykey: ['type', 'production', 'outbound', 'active_day'],
        submitChange(row,index,mykey,newData){
            table10.origin.actual_data[mykey] = newData
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table10.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data
            }
            sendSave(data,func)
        }
    }
    return table10

}
export default table10


