import React, { Component } from 'react'
import  {BrowserRouter,Switch,Route } from 'react-router-dom'
import SimpleReport from './components/simplereport.js'
import MonthlyReport from './components/monthlyreport.js'
export default class Router extends Component {
    render(){
        return (
            <Switch>
                <Route path="/sales/PhoneMonthReport/Total" exact component={SimpleReport} />
                <Route path="/sales/PhoneMonthReport/Detail" component={MonthlyReport} />
            </Switch>
        )
    }
}


