import React, { Component } from 'react'
import { Icon, Tooltip, Button } from 'antd'
import styles from './index.scss'
class SingleTab extends Component {
    constructor(props){
        super(props)
    }
    changeStatus(){
        this.props.changeStatus()
    }
    render(){
        const { title, editing, showModal,save,id, status } = this.props
        return (
            <div id={id}>
                <div className={styles["tab-title"]} style={editing?{}:{}}>
                    <span>{title}</span>
                    <span>{status==1?<Icon type="check" style={{color:'#009a61',fontSize:'25px',fontWeight:'800'}}/>:''}</span>
                    <Tooltip title={editing?'返回(不保存)':'数据编辑视图'}>
                        <Icon type={editing?"left":"form"} onClick={()=>this.changeStatus()} />
                    </Tooltip>
                </div>
                <div style={{margin:"10px 0"}}>
                    {editing?
                    <div className={styles["table-container"]}>
                        {this.props.children}
                        {showModal?<Button onClick={()=>showModal({
                            title:'保存提示',
                            onOk: save,
                            children:'确定要保存该数据吗？'
                        })}>保存</Button>:''}
                    </div>
                    :
                    <div className={styles['tab_container']}>{this.props.preview}</div>}
                </div>
            </div>
        )
    }
}

export default SingleTab


