import React, { Component } from 'react';
import logo from './logo.svg';
import styles from './App.css';
import Router from './router.js'
import { Menu, Icon, Select, Button, Modal, Anchor, message } from 'antd'
import {Link} from 'react-router-dom'
import Image from './components/image.js'
import help1 from './assets/image/help1.png'
import help2 from './assets/image/help2.png'
import monthPreview from './components/preview/index.js'
import {getDataByMonth} from './components/data/fetchdata'
const { SubMenu, MenuItemGroup } = Menu
const { Option } = Select
const monthArr = ['一','二','三','四','五','六','七','八','九','十','十一','十二']
const src = 'http://student.mtstudent.se/~sh14hp3151/wordpress/wp-content/uploads/2015/01/pluvia-2880x1800.jpg'
const titles = ['标题与背景','关键数据','全球月激活趋势','全球激活机型TOP10','全球激活分国家TOP20','国内激活分区域TOP','国内季度汇总','国内产销趋势','国内渠道达成','国内重点机型达成','国内生产达成']

window.onscroll = (e)=>{
  if(document.documentElement.scrollTop>55){
    document.getElementById('toolbar').classList.add('toolbar-fixed')
    document.getElementById('root').children[0].style.paddingTop = '100px'
    document.getElementById('releaseall').classList.add('btn-fixed')
  }else{
    document.getElementById('toolbar').classList.remove('toolbar-fixed')
    document.getElementById('releaseall').classList.remove('btn-fixed')
    document.getElementById('root').children[0].style.paddingTop = '50px'
  }
} 
window.createMessage = (text)=>{
  message.info(text)
}
function info(){
  Modal.info({
    title: '提示',
    content: (
      <div style={{width:800}}>
        <p>1. <Icon type="check" style={{color:'#009a61',fontSize:'25px',fontWeight:'800'}} />表示该模块已发布</p>
        <p>2. 表格每行后的按钮, <code>x</code> 表示删除该行, <code>+</code> 表示在该行下追加一行 </p>
        <img style={{width:500}} src={help1} />
        <p>3. 表格每列头的按钮, <code>x</code> 表示删除该列, <code>+</code> 表示在该列后追加一列 </p>
        <img style={{width:100,marginLeft:10}} src={help2} />
      </div>
    ),
    className:styles.helpModal,
    onOk(){},
  })
}
class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      current: window.location.pathname.split('?')[0],
      month: new Date().getMonth() + 1,
      preview:false,
      previewData:[]
    }
  }
  toggleMenu(e){
    this.setState({
      current:e.key
    })
  }
  preview(e){
    if(!this.getPreview()){
      return 
    }
    this.setState(prevState=>({
      preview: !prevState.preview
    }))
  }
  getPreview(){
    if(!window.previewTable){
      window.createMessage('请先切换到手机月报再进行预览哦!')
      return false
    }
    let html = [], i = 0
    for(let item of window.previewTable){
      if(i===2 || i===7 || i===10){
        html.push(monthPreview[i](window.previewTable[i].thead[0],window.previewTable[i].content[0]))
      } else{
        html.push(monthPreview[i](window.previewTable[i].thead,window.previewTable[i].content))
      }
      i++
    }
    this.setState({
      previewData: html
    })
    return true
  }
  componentWillMount(){
    window.datas =  getDataByMonth(this.state.month)
    console.log(window.datas,'datas')
    window.month = this.state.month
  }
  render() {
    const { preview,current,month, previewData } = this.state
    const months = monthArr.map((value,index)=>{
      return <Option key={index} value={index+1}>2018-{value}月</Option>
    })
    return (
      <div className={styles.App}>
        <Menu className={styles.menu} mode="horizontal" selectedKeys={[current]} onClick={e=>this.toggleMenu(e)}>
          <Menu.Item key="/sales/PhoneMonthReport/Total">
          <Link to="/sales/PhoneMonthReport/Total">
            手机月度简报
            </Link>
          </Menu.Item>
          <Menu.Item key="/sales/PhoneMonthReport/Detail">
          <Link to='/sales/PhoneMonthReport/Detail'>
          手机月报
            </Link>
          </Menu.Item>
        </Menu>
        <div id="toolbar" className={styles.toolbar}>
          <Select
            className={styles.select}
            dropdownClassName={styles.scrollbar}
            defaultValue={this.state.month}
            onSelect={(value)=>{
              window.datas =  getDataByMonth(value)
              window.month = value
              this.setState({
                month:value
              })
            }}
            style={{ width: 200,
              marginTop:20,
              borderBottomRightRadius: 0,
              borderTopRightRadius: 0, 
              }} 
            placeholder="月份">

            {months}

          </Select>
          {current==='/sales/PhoneMonthReport/Detail'&&<Button type="primary" onClick={()=>this.preview()}>预览全图</Button>}
          <Icon style={{
            color:'#b4b4b4',
            fontSize:25,
            border:'1.5px solid #b4b4b4',
            borderRadius:'4px'
            }} onClick={info} type="question" />
        </div>
          <Image className={styles.scrollbar} show={preview} src={previewData} toggle={(e)=>this.preview(e)} >
          </Image>
        <Router></Router>
        <div className={styles["anchor-container"]}>
          {current==='/sales/PhoneMonthReport/Detail'&&<Anchor>
            { titles.map((v,index)=>{
              return <Anchor.Link key={index} href={'#'+v} title={v}></Anchor.Link>
            })}
          </Anchor>}
        </div>
      </div>
    );
  }
}

export default App;



