import { sendSave } from './fetchdata'
const thead = ['序号','机型','激活量']

export function table3(data){
    let content = [], i = 0
    const global_project_active_top = data.data.global_project_active_top
    
    let { actual_data, status } = global_project_active_top
    if(!actual_data){
        global_project_active_top.actual_data = []
        actual_data = global_project_active_top.actual_data
    }
    for(let key in actual_data){
        i++
        content.push(
            [ i, actual_data[key].type, actual_data[key].number]
        )
    }
    const table3 = {
        origin:global_project_active_top,
        content:content,
        thead:thead,
        status,
        mykey:['序号','type','number'],
        submitChange(row,index,mykey,newData){
            if(mykey=='number') newData = Number(newData)
            table3.origin.actual_data[row][mykey] = newData
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table3.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data
            }
            sendSave(data,func)
        }
    }
    return table3

}
export default table3;


