import React, { Component } from "react";
import '../css/style.css'
import line from '../img/line.png'

export default function domeAll(title,content){
    return (
        <div className="container">
        <div className='models last-total'>
            <div className="title-wrap">
                <p className="production-title">国内季度汇总</p>
                <img src={line} alt="line" className="production-bg" />
            </div>
            <div className="models-item-content">
                <table>
                    <thead>
                        <tr className="t-title">
                        {title.map((t,index)=>{
                            return <td key={index}>{t}</td>
                        })}
                        </tr>
                        </thead>

                    <tbody>
                        {content.map((c,i)=>{
                            i = i+1
                            return (
                                <tr key={i}>
                                 {c.map((single,index)=>{
                                    return <td key={index}>{single}</td>
                                })}
                                </tr>
                            )
                        })}
                    </tbody>
                </table>
                <p className="comments">单位&nbsp;&nbsp;&nbsp;/&nbsp;&nbsp;&nbsp;万台</p>
            </div>
        </div>
        </div>
    )
}


