import React, { Component } from "react";
import '../css/style.css'
import line from '../img/line.png'
const index2Num = ['special-one','special-two','special-three','special-four','special-five']
export default function domeChannelReach(title,content){
    return (
        <div className="container">
        <div className='models'>
            <div className="title-wrap">
                <p className="production-title">国内渠道达成</p>
                <img src={line} alt="line" className="production-bg" />
            </div>
            <div className="models-item-content">
                <table>
                        <thead>
                        <tr className="t-title">
                        {title.map((t,index)=>{
                            return <td key={index}>{t}</td>
                        })}
                        </tr>
                        </thead>
                    <tbody>
                        {content.map((c,i)=>{
                            i = i+1
                            return (
                                <tr key={i}>
                                 {c.map((single,index,cc)=>{
                                    return <td key={index} className={index2Num[index]}>{index==cc.length-1?(single+'%'):(index==0?single:Number(single).toFixed(1))}</td>
                                })}
                                </tr>
                            )
                        })}
                    </tbody>
                </table>
                <p className="comments">单位&nbsp;&nbsp;&nbsp;/&nbsp;&nbsp;&nbsp;万台</p>
            </div>
        </div>
        </div>
    )
}


