import { sendSave } from './fetchdata'
const mykey = ['序号','type','number']
const thead = ['序号','国家','激活量']
export function table4(data){
    let { actual_data, status } = data.data.global_country_active_top
    const content = []
    let i = 0
    if(!actual_data){
        data.data.global_country_active_top.actual_data = []
        actual_data = data.data.global_country_active_top.actual_data
    }
    for(let item of actual_data){
        i++
        content.push(
            [i,item.type,item.number]
        )
    }
    const table4 = {
        origin:data.data.global_country_active_top,
        thead,
        content,
        mykey,
        status,
        submitChange(row,index,mykey,newData){
            if(mykey=='序号'){
                return
            }
            if(mykey=='number') newData = Number(newData)
            table4.origin.actual_data[row][mykey] = newData
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table4.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data
            }
            sendSave(data,func)
        }
    }
    return table4

}
export default table4


