import React, { Component } from "react";
import '../css/style.css'
import line from '../img/line.png'

export default function domeProduct(title,content){
    let axis = window.previewTable[10].content[1]
    let colors = window.previewTable[10].content[2][0]

    let y1 = axis[0]
    let y2 = axis[1]

    let min1 = y1[1]?{min:y1[1]}:{}
    let max1 = y1[2]?{max:y1[2]}:{}
    let tickInterval1 = y1[3]?{tickInterval:y1[3]}:{}

    let min2 = y2[1]?{min:y2[1]}:{}
    let max2 = y2[2]?{max:y2[2]}:{}
    let tickInterval2 = y2[3]?{tickInterval:y2[3]}:{}
    console.log(axis,'table')
    setTimeout(()=>{

//国内生产达成
window.$('#domesticProduction').highcharts({
    chart: {
        zoomType: 'xy',
        backgroundColor:'transparent',
        renderTo: 'domesticProduction',
        options3d: {
            enabled: true,
            alpha: 20,
            beta: 0,
            viewDistance: 25,
            depth: 0,
        },
    },
    title: {text: null},
    credits:{enabled:false},
    plotOptions:{
        spline:{
            lineWidth:6,
        },
        series: {
            borderRadius: 7,
        }
    },
    xAxis: [{
        tickWidth:0,
        categories: content.map(v=>v[0]),
        gridLineWidth:0,
        labels:{
            x: 0,
            y: 80,
            rotation: -60,
            formatter: function () {
                return this.value;
            },
            style:{
                color: '#fff',
                fontSize: '23px', 
            },
        },
    }],
    yAxis: [{ // Primary yAxis
        ...min1,
        ...max1,
        ...tickInterval1, // Primary yAxis
        labels: {
            style: {
                color: '#fff',
                fontSize: '23px',
            }
        },
        gridLineColor:'rgba(153,153,153,.3)',
        gridLineWidth: 1,
        title: {
            enabled:false
        }
    }, { // Primary yAxis
        ...min2,
        ...max2,
        ...tickInterval2, // Secondary yAxis
        title: {
            enabled:false
        },
        labels: {
            format: '{value}%',
            style: {
                color: "#fff",
                fontSize: '23px',
            }
        },
        opposite: true
    }],
    legend: {
        verticalAlign: 'top',
        itemDistance:30,
        itemStyle:{
            color: '#fff',
            fontSize: '25px',
        },
        margin:100,
        symbolPadding: 20,
        symbolWidth: 30
    },
    tooltip:{
        enabled:false
    },
    series: [{
        name: '生产量',
        type: 'column',
        yAxis:1,
        color:'rgba(137,173,182,.8)',
        data: content.map(v=>Number(v[1]))
    },{
        name: '生产达成',
        type: 'spline',
        color:'rgba(201,166,152,.8)',
        data: content.map(v=>Number(v[2]))
    }]
});
    })
    return (
        <div className="container">
        <div className="domesticProduction">
            <div className="title-wrap">
                <p className="production-title">国内生产达成</p>
                <img src={line} alt="line" className="production-bg" />
            </div>
            <div id="domesticProduction"></div>
            <p className="comments">单位&nbsp;&nbsp;&nbsp;/&nbsp;&nbsp;&nbsp;万台</p>
        </div>
    <img src="/images/Shape-2.png" alt="" className="btm-bg" />
    <p className="btm-p">小米信息部</p>
        </div>
    )
}


