import { sendSave } from './fetchdata'
const thead = [
    ['月份(倒推12个月)','生产','出库','激活'],
    ['坐标轴','最小值','最大值','间隔单位'],
    ['颜色','1','2','3']
]
const mykey = [
['月份','production','outbound','active'],
['','min','max','gap'],
['','color1','color2','color3']
]
let axis = [
    ['左（主要）','','','']
]
let colors = [
    ['注释内容','','','']
]

export function table6(data){
    let { actual_data, status, config_data } = data.data.dome_production_sell_trend
    const maintain = []
    if(!actual_data){
        data.data.dome_production_sell_trend.actual_data = {}
        actual_data = data.data.dome_production_sell_trend.actual_data
    }
    if(config_data){
        axis = JSON.parse(window.HTMLDecode(config_data)).axis
    }
    
    const content = [
        maintain
    ,
        axis
    ,
        colors
    ]
    for(let key in actual_data.production){
        maintain.push(
            [ key, actual_data.production[key] , actual_data.outbound[key], actual_data.active[key]]
        )
    }
    const table6 = {
        thead: thead,
        content: content,
        origin: data.data.dome_production_sell_trend,
        mykey:mykey,
        status,
        submitChange(row,index,mykey,newData){
            //获取月份
            let type = Object.keys(table6.origin.actual_data.production)[row]

            if(['min','max','gap'].indexOf(mykey)>-1){
                mykey==='min'&&(content[1][0][1] = newData)
                mykey==='max'&&(content[1][0][2] = newData)
                mykey==='gap'&&(content[1][0][3] = newData)
                return
            }
            if(['color1','color2','color3'].indexOf(mykey)>-1){
                mykey==='color1'&&(content[2][0][1] = newData)
                mykey==='color2'&&(content[2][0][2] = newData)
                mykey==='color3'&&(content[2][0][3] = newData)
                return
            }
            if(mykey==='月份'){
                for(let key in table6.origin.actual_data){
                    table6.origin.actual_data[key][newData] = table6.origin.actual_data[key][type]
                    table6.origin.actual_data[key][type] = null
                }
            }else{
                table6.origin.actual_data[mykey][type] = newData
            }
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table6.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data,
                config_data: {
                    axis,
                    colors,
                }
            }
            sendSave(data,func)
        }
    }
    return table6

}
export default table6


