import { sendSave } from './fetchdata'
const thead = ['渠道','月预测','出库','激活量','出库达成']

export function table7(data){
    let content = []
    const dome_channel_finish = data.data.dome_channel_finish
    let { actual_data,status } = dome_channel_finish
    if(!actual_data){
        data.data.dome_channel_finish.actual_data = []
        actual_data = data.data.dome_channel_finish.actual_data
    }

    for(let item of actual_data){
        content.push(
            [ item.type, item.forecast,item.outbound, item.active_day,item.finish_rate]
        )
    }

    let table7 = {
        origin: dome_channel_finish,
        content,
        thead,
        status,
        mykey:['type','forecast','outbound','active_day','finish_rate'],
        submitChange(row,index,mykey,newData){
            table7.origin.actual_data[row][mykey] = newData
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table7.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data
            }
            sendSave(data,func)
        }
    }
    return table7
}

export default table7;


