import { sendSave } from './fetchdata'
const thead = ['序号','区域','激活量']
const mykey = ['序号','type','number']
export function table5(data){
    const content = []
    let { actual_data, status } = data.data.dome_region_active_top
    let i = 0
    if(!actual_data){
        data.data.dome_region_active_top.actual_data = []
        actual_data = data.data.dome_region_active_top.actual_data
    }
    for(let item of actual_data){
        i++
        content.push(
            [i,item.type,item.number]
        )
    }
    const table5 = {
        origin:data.data.dome_region_active_top,
        thead,
        content,
        mykey,
        status,
        submitChange(row,index,mykey,newData){
            if(mykey=='序号'){
                return
            }
            if(mykey=='number') newData = Number(newData)
            table5.origin.actual_data[row][mykey] = newData
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table5.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data
            }
            sendSave(data,func)
        }
    }
    return table5

}
export default table5


