import { sendSave } from './fetchdata'
const thead = ['机型','月预测','出库','激活量','出库达成']
export function table8(data){
    let content = []
    const dome_project_finish = data.data.dome_project_finish
    
    let { actual_data, status } = dome_project_finish
    if(!actual_data){
        data.data.dome_project_finish.actual_data = []
        actual_data = data.data.dome_project_finish.actual_data
    }
    for(let item of actual_data){
        content.push(
            [ item.type, item.forecast,item.outbound, item.active_day,item.finish_rate]
        )
    }
    const table8 = {
        origin: dome_project_finish,
        content,
        thead,
        status,
        mykey:['type','forecast','outbound','active_day','finish_rate'],
        submitChange(row,index,mykey,newData){
            table8.origin.actual_data[row][mykey] = newData
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table8.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data
            }
            sendSave(data,func)
        }
    }
    return table8

}
export default table8;


