import React, { Component } from 'react'
import SingleTab from './singletab.js'
import Table from './editableTable/table.js'
import { sendRelease } from './data/fetchdata'
import { Button, Modal } from 'antd'
import './css/briefReport.css'
import preview from './preview/simple.js'
import table from './data/simple'

class SimpleReport extends Component {
    constructor(props){
        super(props)
        this.state = {
            visible:false,
            title:'',
            editing:false,
            month:window.month,
            modalOption:{
                visible:false,
                title:"提示",
                onOk: this.handleOk,
                onCancel: this.handleCancel,
                children:'',
                confirmLoading:false
            }
        }
        this.handleCancel = this.handleCancel.bind(this)
        this.handleOk = this.handleOk.bind(this)
    }
    handleOk(){
        this.state.modalOption.visible = false
        this.setState({
            modalOption:this.state.modalOption,
            editing: false
        })
    }
    handleCancel(){
        this.state.modalOption.visible = false
        this.setState({
            modalOption:this.state.modalOption,
        })
    }
    changeStatus(){
        if(this.state.editing){
            this.showModal({
                title:'返回提示',
                onCancel: '',
                children:'有未经保存或发布的数据，确认要返回图表视图吗？返回后不可恢复'
            })
        }else {
            this.setState({
                editing: true
            })
        }
    }
    showModal({title,onOk,onCancel,children}){
        onCancel = onCancel || this.handleCancel
        onOk = onOk || this.handleOk
        this.setState({
            modalOption:{
                visible:true,
                title,
                onOk,
                onCancel,
                children,
                confirmLoading:false
            }
        })
    }
    loading(){
        this.setState({
            modalOption:{...this.state.modalOption,confirmLoading:true}
        })
    }
    release(){
        let _ = this
        sendRelease({
            month: window.month,
            modules_id: window.datas.data.phone_month_total.modules_id,
            modules: window.datas.data.phone_month_total.modules,
            status:1,
            func:_.handleOk
        })
    }
    componentWillReceiveProps(nextProps){
        if(window.month !=this.state.month){
            this.setState({
                editing:false
            })
        }
    }
    render(){
        let simpleData = window.datas.data.phone_month_total
        const { editing, modalOption} = this.state
        const { title, visible, onOk, onCancel, children, confirmLoading } = modalOption
        const month = window.month
        let {content,mykey,submitChange,thead,save, status} = table(simpleData)
        thead[0] = ['手机'+month+'月简报']
        return (
            <div>
                    <SingleTab title="手机简报关键数据" cancel={()=>this.setState({visible:true})} editing={editing} changeStatus={()=>this.changeStatus()}
                  status={status}
                  preview={
                      preview(thead,content)
                  }>
                        <Table title={thead[0]} content={content[0]} mykey={mykey[0]} submitChange={(...args)=>{submitChange(...args,0)}}/>
                        <Table title={thead[1]} content={content[1]} mykey={mykey[1]} submitChange={(...args)=>{submitChange(...args,1)}}/>
                        <Table title={thead[2]} content={content[2]} mykey={mykey[2]} submitChange={(...args)=>{submitChange(...args,2)}}/>
                    </SingleTab>
                <div id="releaseall">
                    <Button onClick={()=>this.showModal({
                        title:'保存提示',
                        onOk: ()=>{
                            this.loading();
                            save(month,this.handleOk)
                        },
                        children:'确定要保存该数据吗？'
                    })}>保存</Button>

                    <Button
                      onClick={()=>this.showModal({
                      title:'发布提示',
                      onOk: ()=>{
                        this.loading();
                        this.release();
                      },
                      children:'确认要发布该数据吗？发布后将同步显示到安卓APP端小米人'
                        })}>
                      发布
                    </Button>
                </div>
                <Modal title={title} visible={visible} onOk={onOk} onCancel={onCancel} confirmLoading={confirmLoading}>
                    {children}
                </Modal>
            </div>
        )
    }
}

export default SimpleReport
