import React,{Component} from 'react'
import styles from './table.scss'
import { Tooltip } from 'antd'
import { funTransitionHeight, funTransitionWidth} from '../../utils/common.js'
let mykey = []
//表格标题
class Title extends Component{
    constructor(props){
        super(props)
        this.state = {
            title:props.title||'',
            edit:false,
            index:-1
        }
    }
    handleClick(e,title){
        if(e.target.nodeName.toLowerCase() === 'input') return 
        if(!this.input.style.width){
            this.input.style.width = parseInt(window.getComputedStyle(this.container).width)*0.8 + 'px';
        }
        this.setState({
            edit:true
        },()=>{
            let v = this.input.value
            this.input.value = ''
            this.input.focus()
            this.input.value = v
        })
    }
    handleChange(event){
        this.input.style.width = parseInt(window.getComputedStyle(this.container).width)*0.8 + 'px';
        this.setState({
            title:event.target.value
        })
    }
    handleBlur(){
        this.setState({
            edit:false
        })
        this.props.changeTitle(this.state.index,this.state.title)
    }
    onKeyUp(e){
        e.keyCode === 13 && this.handleBlur()
    }
    componentDidMount(){
    }
    render(){
        const {mykey} = this.props
        console.log(mykey)
        let title = this.state.title
        this.state.index = this.props.index
        return (
            <td data-mykey={mykey} ref={(div)=>this.container = div} className={styles["column-title"]} data-key={this.props.index} onClick={(e)=>this.handleClick(e,title)}>
                <Tooltip overlayClassName={styles["opt-btn"]} title={
                    <div className={styles["operation-btn"]}>
                    <span onClick={()=>this.props.del_column(this.props.index)}>x</span>
                    <span onClick={()=>this.props.add_column(this.props.index)}>+</span>
                    </div>}>
                <div className={this.state.edit?styles["hide"]:''}>{title}</div>
                </Tooltip>
                <input type="text" ref={input=>this.input = input} className={this.state.edit?'':styles["hide"]} onChange={(event)=>this.handleChange(event)} value={this.state.title} onBlur={()=>this.handleBlur()} onKeyUp={(e)=>this.onKeyUp(e)}/>
                
            </td>
        )
    }
    }
class Single extends Component {
    constructor(props){
        super(props)
        this.state = {
            num:0,
            content:props.content==null?'':props.content,
            edit:false
        }
    }
    handleClick(e,title){
        if(e.target.nodeName.toLowerCase() === 'input') return 
        if(!this.input.style.width){
            this.input.style.width = parseInt(window.getComputedStyle(this.container).width)*0.8 + 'px';
        }
        this.setState({
            edit:true
        },()=>{
            let v = this.input.value
            this.input.value = ''
            this.input.focus()
            this.input.value = v
        })
    }
    handleChange(event){
        this.input.style.width = parseInt(window.getComputedStyle(this.container).width)*0.8 + 'px';
        this.setState({
            content:event.target.value
        })
    }
    handleBlur(){
        const mykey = this.props.mykey
        console.log(mykey,'111111')
        this.setState({
            edit:false
        })
        this.props.changeContent(this.props.row,this.props.index,this.state.content)
        console.log(this.props.row,this.props.index,mykey)
        this.props.submitChange(this.props.row,this.props.index,mykey,this.state.content)
    }
    onKeyUp(e){
        e.keyCode === 13 && this.handleBlur()
    }
    render(){
        let content = this.state.content
        return (
            <td ref={(div)=>this.container = div} className={styles["column-title"]} data-key={this.props.index} onClick={(e)=>this.handleClick(e,content)}>
                <div className={this.state.edit?styles["hide"]:''}>{content}</div>
                <input type="text" ref={input=>this.input = input} className={this.state.edit?'':styles["hide"]} onChange={(event)=>this.handleChange(event)} value={this.state.content} onBlur={()=>this.handleBlur()} onKeyUp={(e)=>this.onKeyUp(e)}/>
            </td>
        )
    }
}
class Content extends Component {
    constructor(props){
        super(props)
        this.state = {
            num:0
        }
    }
    render(){
        let {submitChange, mykey} = this.props
        let row = Number(this.props.row)
        let list = this.props.content.map((value,index)=>{
            this.state.num++
            return <Single
              mykey={mykey[index]}
              submitChange={submitChange}
             key={this.state.num} content={value} changeContent={this.props.changeContent} row={this.props.row} index={index} />
        })
        return (
            <tr className={styles["row-content"]}>
                {list}
                <td>
                    <div className={styles["operation-btn"]}>
                        <span onClick={()=>this.props.del_row(row)}>x</span>
                        <span onClick={()=>this.props.add_row(row+1)}>+</span>
                    </div>
                </td>
            </tr>
        )
    }
}
class Table extends Component {
    constructor(props){
        super(props)
        this.state = {
            titles: props.title,
            content:props.content,
            column_num:0,
            row_num:0
        }
        this.add_column = this.add_column.bind(this)
        this.del_column = this.del_column.bind(this)
        this.changeTitle = this.changeTitle.bind(this)
        this.changeContent = this.changeContent.bind(this)
        this.add_row = this.add_row.bind(this)
        this.del_row = this.del_row.bind(this)
    }
    add_column(index){
        index = Number(index) + 1
        this.state.titles.splice(index,0,'')
        this.state.content.map((item)=>{
            item.splice(index,0,'')
        })
        this.setState(prevState=>({
            titles:prevState.titles,
            content:prevState.content
        }),()=>{
            let tds = this.tr.children
            for(let i = 0;i<tds.length;i++){
                if(tds[i].dataset.key == index){
                    tds[i].click()
                }
            }
        })
    }
    del_column(index){
        this.state.titles.splice(index,1)
        this.state.content.map((item)=>{
            item.splice(index,1)
        })
        this.setState((prevState)=>({
            titles:prevState.titles,
            content:prevState.content
        }))
    }
    changeTitle(index,title){
        this.state.titles[index] = title
        this.setState((prevState)=>({
            titles:prevState.titles
        }))
    }
    changeContent(row,index,content){
        this.state.content[row][index] = content
        this.setState((prevState)=>({
            content:prevState.content
        }))
    }
    add_row(index){
        let ln = this.state.titles.length
        this.state.content.splice(index,0,Array(ln).fill(''))
        this.setState((prevState)=>({
            content: prevState.content
        }))
    }
    del_row(index){
        this.state.content.splice(index,1)
        this.setState((prevState)=>({
            content: prevState.content
        }))
    }
    render(){
        mykey = this.props.mykey
        let { submitChange, title, content} = this.props
        const tabletitle = this.state.titles&&this.state.titles.map((item,index)=>{
            this.state.column_num++
            return <Title mykey={mykey[index]} key={this.state.column_num} title={item} index={index} add_column={this.add_column} changeTitle={this.changeTitle} del_column={this.del_column} />
        })
        const tablecontent = this.state.content&&this.state.content.map((item,index)=>{
            this.state.row_num++
            return (
                <Content
                  mykey={mykey}
                  submitChange={submitChange}
                 key={this.state.row_num} content={item} changeContent={this.changeContent} row={index} add_row={this.add_row} del_row={this.del_row} />
            )
        })
        return (
            <div>
                <table style={{marginLeft:"10px"}}>
                    <thead>
                        <tr ref={tr=>this.tr=tr} className={styles["rowcontent"]}>
                            {tabletitle}
                            <td className={styles["last-add"]}><span onClick={()=>this.add_row(0)}>+</span></td>
                        </tr>
                    </thead>
                    <tbody>
                        {tablecontent}
                    </tbody>
                </table>
            </div>
        )
    }
}

export default Table


