import React from 'react'
import '../css/style.css'
import line from '../img/line.png'
const $ = window.$
function getLastDay(year,month)        
{        
 var new_year = year;    //取当前的年份        
 var new_month = month++;//取下一个月的第一天，方便计算（最后一天不固定）        
 if(month>12)            //如果当前大于12月，则年份转到下一年        
 {        
  new_month -=12;        //月份减        
  new_year++;            //年份增        
 }        
 var new_date = new Date(new_year,new_month,1);                //取当年当月中的第一天        
 return (new Date(new_date.getTime()-1000*60*60*24)).getDate();//获取当月最后一天日期        
} 
export default function previewKey(title,content){
    let data = window.datas.data.phone_month_total.actual_data
    setTimeout(()=>{

    $('#globalProduction').particleground();
    $('#globalOutbound').particleground();
    $('#domesticOutbound').particleground();
    $('#overseasOutbound').particleground();
    })
    let arr = ['global_outbound','global_production','dome_outbound','overseas_outbound']
    for(let key of arr){
        if(!data[key]){
            data[key] = {
                num:0
            }
        }
    }
return(
<div className="container">
    <div className="production-wrap">
        <div className="title-wrap">
            <p className="production-title">全球生产</p>
            <img src="/images/line_1.png" alt="line" className="production-bg-top" />
        </div>
        <img src="/images/Group1.png" alt="" id="globalProduction" />
        <div className="global-production">
            <span className="production-number">{data.global_production?data.global_production.num:0}</span>
            <span className="global_product">万台</span>
        </div>
        <p className="centerp">取自SRM中实际生产数，{getLastDay(2018,window.month)}为计划数</p>
    </div>
    { /*全球出库*/ }
    <div className="production-wrap">
        <div className="title-wrap">
            <p className="production-title">全球出库</p>
            <img src="/images/Rectangle3.png" alt="line" className="production-bg-top" />
        </div>
        <img src="/images/line2.png" alt="" id="globalOutbound" />
        <div className="global-production">
            <span className="production-number">{data.global_outbound?data.global_outbound.num:0}</span>
            <span>万台</span>
        </div>
        <p className="centerp">包含印尼 {data.global_outbound.indonesia}万 机头出库</p>
    </div>
    { /*国内出库*/ }
    <div className="production-wrap">
        <div className="title-wrap">
            <p className="production-title">国内出库</p>
            <img src="/images/Rectangle2.png" alt="line" className="production-bg-top" />
        </div>
        <img src="/images/line3.png" alt="" id="domesticOutbound" />
        <div className="global-production">
            <span className="production-number">{data.dome_outbound.num}</span>
            <span>万台</span>
        </div>
    </div>
    {/*海外出库*/ }
    <div className="production-wrap">
        <div className="title-wrap">
            <p className="production-title">海外出库</p>
            <img src="/images/Rectangle4.png" alt="line" className="production-bg-top" />
        </div>
        <img src="/images/line4.png" alt="" id="overseasOutbound" />
        <div className="global-production">
            <span className="production-number">{data.overseas_outbound.num}</span>
            <span>万台</span>
        </div>
        <p className="centerp">
            <span className="text1">印度：{data.overseas_outbound.india}万台</span>
            <span className="text2">印尼：{data.overseas_outbound.indonesia}万台（机头）</span>
        </p>
    </div>
    </div>
)
}


