import React, { Component } from 'react'
import SingleTab from './singletab.js'
import Table from './editableTable/table.js'
import { Button, Modal } from 'antd'
import { sendRelease,getDataByMonth } from './data/fetchdata'
import preview from './preview/index.js'
import createTable from './data/index'
const titles = ['标题与背景','关键数据','全球月激活趋势','全球激活机型TOP10','全球激活分国家TOP20','国内激活分区域TOP','国内季度汇总','国内产销趋势','国内渠道达成','国内重点机型达成','国内生产达成']

class MonthlyReport extends Component {
    constructor(props){
        super(props)
        this.state = {
            editing:[],
            modalOption:{
                visible:false,
                title:"提示",
                onOk: this.handleOk,
                onCancel: this.handleCancel,
                children:'',
                month:window.month,
                confirmLoading:false
            }
        }
        this.cancel = this.cancel.bind(this)
        this.showModal = this.showModal.bind(this)
        this.changeStatus = this.changeStatus.bind(this)
        this.hideModal = this.hideModal.bind(this)
        this.release = this.release.bind(this)
    }
    loading(){
        this.setState({
            modalOption:{...this.state.modalOption,confirmLoading:true}
        })
    }
    cancel(i){
        let newEditing = this.state.editing
        newEditing[i] = !newEditing[i]
        this.setState({
            editing: newEditing
        })
        this.hideModal()
    }
    changeStatus(i){
        let _ = this
        if(this.state.editing[i]){
            this.showModal({
                title:'返回提示',
                onOk:()=>{
                    _.cancel(i)
                },
                onCancel: null,
                children:'有未经保存或发布的数据，确认要返回图表视图吗？返回后不可恢复'
            })
        }else {
            _.cancel(i)
        }
    }
    showModal({title,onOk,onCancel,children}){
        onCancel = onCancel || this.hideModal
        onOk = onOk || this.hideModal
        this.setState({
            modalOption:{
                visible:true,
                title,
                onOk,
                onCancel,
                children,
                confirmLoading:false,
            }
        })
    }
    hideModal(){
        this.setState({
            modalOption:{ ...this.state.modalOption, visible:false }
        })
    }
    release(){
        let _ = this
        sendRelease({
            month: window.month,
            func:()=>{
                let newEditing =  _.state.editing
                let n = newEditing.map(v=>{
                    return v = false
                })
                console.log(n)
                _.setState({
                    editing: n
                },()=>{
                    _.hideModal()
                    console.log(_.state)
                })
            }
        })
    }
    componentWillReceiveProps(nextProps){
        let newEditing = this.state.editing.map(i=>{
            i = false
        })
        if(window.month !=this.state.month){
            this.setState({
                editing:newEditing
            })
        }
    }
    render(){
        let data = window.datas
        console.log(data,'new')
        let month = window.month
        let table = createTable(data)
        table[0] = {
            thead:['手机'+month +'月战报'],
            content:[['数据抓取时间 03月01日 00:00，可能存在1%以内的误差']],
            mykey:[''],
            submitChange(){},
            save(){}
        }
        window.previewTable = table
        const { editing, modalOption } = this.state
        const { title, visible, onOk, onCancel, children, confirmLoading } = modalOption
        const Items = titles.map((value,i)=>{
            let {content,mykey,submitChange,thead,save, status} = table[i]
           //全球月激活趋势
            if(i===2 || i===7 || i===10){
                return (
                    <SingleTab
                      status={status}
                      id={value} key={i} title={value} cancel={()=>this.cancel(i)} editing={editing[i]} changeStatus={()=>this.changeStatus(i)}
                      preview={preview[i](thead[0],content[0])} showModal={this.showModal}
                      save={()=>{
                          this.loading();
                          save(month,()=>this.cancel(i))}}>
                        <Table title={thead[0]} content={content[0]} mykey={mykey[0]} submitChange={submitChange}/>
                        <Table title={thead[1]} content={content[1]} mykey={mykey[1]} submitChange={submitChange}/>
                        <Table title={thead[2]} content={content[2]} mykey={mykey[2]} submitChange={submitChange}/>
                    </SingleTab>
                )
            }
            //其他
            return (
                <SingleTab
                  status = {status}
                  id={value} key={i} title={value} cancel={()=>this.cancel(i)} editing={editing[i]} changeStatus={()=>this.changeStatus(i)}  save={save}
                  preview={preview[i](thead,content)} showModal={this.showModal} save={()=>{this.loading();save(month,()=>this.cancel(i))}}>
                    <Table mykey={mykey} title={thead} content={content} submitChange={submitChange}/>
                </SingleTab>
            )
        })
        return (
            <div>
                {Items}
                <div id="releaseall">
                    <Button
                      onClick={()=>this.showModal({
                        title:'发布提示',
                        onOk: ()=>{
                            this.loading();//加载动画
                            this.release()//发布请求
                        },
                        children:'确认要发布该数据吗？发布后将同步显示到安卓APP端小米人'
                        })
                      }>发布</Button>
                </div>
                <Modal
                  confirmLoading={confirmLoading}
                  title={title}
                  visible={visible}
                  onOk={onOk}
                  onCancel={onCancel}>

                  {children}

                </Modal>
            </div>
        )
    }
}

export default MonthlyReport


