import React from 'react'
import $ from 'jquery'
export function getDataByMonth(month=1,func){
    let data1
    if(month<10){
        console.log(month)
        month = '0'+ month
    }
    let url = 1?'https://www.easy-mock.com/mock/5ab39527e7fdd05f8942aaff/example/03':'/sales/PhoneMonthReport/Detail?year='
    $.ajax({
        url:url,//+new Date().getFullYear() + '&month=' + month,
        type:'get',
        async:false,
        dataType : "json",
        success(data){
            data1 = JSON.parse(JSON.stringify(data))
            func&&func()
        },
        error(data){
            data1 = {
                data:{
                    length:0,
                    phone_month_total:
                    {
                        actual_data:{}
                    }
                }
            }
            window.createMessage&&window.createMessage('请求失败！')
            func&&func()
        }
    })
    return data1
}
export function sendSave(options,func){
    let { url, year, month, modules_id,modules, status, data, config_data} = options
    let config = ''
    month = Number(month)
    if(month<10){
        month = '0' + month
    }
    year = new Date().getFullYear()
    let json = JSON.stringify(data)
    if(config_data){
    config = JSON.stringify(config_data)}

    $.ajax({
        url: '/sales/PhoneMonthReport/ModifyBasicDataByModules?year=' + year + '&month=' + month + '&modules_id=' + modules_id + '&modules=' + modules + '&status=' + status + '&data=' + json + (config_data?('&config_data=' + config):''),
        type:'get',
        dataType : "json",
        success(data){
            if(data.code === 10000){
                func()
                window.createMessage&&window.createMessage('保存成功!')
            }else{
                func()
                window.createMessage&&window.createMessage('保存失败，请重试!')
            }
        },
        error(){
            func()
            window.createMessage&&window.createMessage('请求失败，请重试!')
        }
    })
}
export function sendRelease(options){
    let { url, year, month, modules_id, modules, status, category,func } = options
    if(month<10){
        month = '0'+ month
    }
    year = year||new Date().getFullYear()
    $.ajax({
        url: url||'/sales/PhoneMonthReport/PublishBasicDataByModules?year='+ year + '&month=' + month + '&status=1'+ (modules?('&modules='+modules + '&modules_id=' + modules_id):'&category=phone_month_report'),
        type:'get',
        dataType: "json",
        success(data){
            if(data.code === 10000){
                func()
                window.createMessage&&window.createMessage('发布成功!')
            }else{
                func()
                window.createMessage&&window.createMessage('发布失败!')
            }
        },
        error(){
            func()
            window.createMessage&&window.createMessage('请求失败，请重试!')
        }
    })
}


