import React, { Component } from "react";
import '../css/briefReport.css'
import line from '../img/line.png'
const origindata = {
    dome_active:0,
    overseas_active:0,
    global_active:0,
}
export default function globalActive(title,content){
    let axis = window.previewTable[2].content[1][0]
    let colors = window.previewTable[2].content[2][0]
    console.log(colors,'table')
    let total = window.datas.data.global_month_active.actual_data.total
    let min = axis[1]?{min:axis[1]}:{}
    let max = axis[2]?{max:axis[2]}:{}
    let tickInterval = axis[3]?{tickInterval:axis[3]}:{}
    console.log(min,axis[1])
    if(!total){
        window.datas.data.global_month_active.actual_data.total = origindata
        total = window.datas.data.global_month_active.actual_data.total
    }
    setTimeout(()=>{
        window.$('#activationTrends').highcharts({
            chart: {
                zoomType: 'xy',
                backgroundColor:'transparent',
            },
            title: {text: null},
            credits:{enabled:false},
            legend: {
                verticalAlign: 'top',
                labelFormatter: function () {
                    return this.name;
                },
                itemDistance:30,
                itemStyle:{
                    color: '#fff',
                    fontSize: '25px',
                },
                margin:100,
                symbolPadding: 20,
                symbolWidth: 30
            },
            tooltip:{enabled:false},
            plotOptions:{
                spline:{
                    lineWidth:6,
                },
                series: {
                    fillOpacity: 0.1,
                },
                area: {
                    lineColor:'transparent',
                    marker: {
                        enabled: false,
                        symbol: 'circle',
                        radius: 2,
                    }
                }
            },
            xAxis: [{
                tickWidth:0,
                categories: content.map(v=>v[0]),
                gridLineWidth:0,
                tickInterval:1,
                labels:{
                    x: 0,
                    y: 80,
                    formatter: function () {
                        return this.value;
                    },
                    style:{
                        color: '#fff',
                        fontSize: '23px', 
                    },
                },
            }],
            yAxis: [{ // Primary yAxis
                ...min,
                ...max,
                ...tickInterval,
                labels: {
                    style:{
                        color: '#fff',
                        fontSize: '23px', 
                    },
                },
                gridLineColor:'rgba(153,153,153,.3)',
                gridLineWidth: 1,
                title: {
                    //enabled:false
                }
            }, { // Secondary yAxis
                title: {
                    enabled:false
                },
                labels: {
                    enabled:false
                },
            }],
            
            series: [{
                name: '国内 ('+ total.dome_active +'万)',
                type: 'spline',
                color:'#F2CEB7',
                data: content.map(v=>Number(v[2])),
            }, {
                name: '海外 ('+ total.overseas_active +'万)',
                type: 'spline',
                color:'#99C0CF',
                data: content.map(v=>Number(v[3])),
            },{
                name: '全球 ('+ total.global_active +'万)',
                type: 'area',
                data: content.map(v=>Number(v[1])),
            }]
        })
    })
    return (
        <div className="container">

    <div className="activationTrends">
        <div className="title-wrap">
            <p className="production-title">全球月激活趋势</p>
            <img src={line} alt="line" className="production-bg" />
        </div>
        <div id="activationTrends"></div>
        <p className="comments">单位&nbsp;&nbsp;&nbsp;/&nbsp;&nbsp;&nbsp;万台</p>
    </div>
    </div>
    )
}


