import { sendSave } from './fetchdata'
const row2Key = ['global_production','global_outbound','dome_outbound','overseas_outbound','indonesia1','india','indonesia2']
const index2Key = ['标题','num','注释']
const thead = ['标题','数量','注释']
const origindata = {
    global_production:{
        num:0
    },
    global_outbound:{
        num:0,
    },
    dome_outbound:{
        num:0
    },
    overseas_outbound:{
        num:0,
    }
}
export function table2(data){
    let pmt = data.data.phone_month_total
    console.log(pmt.actual_data,'zhende')
    if(!pmt.actual_data){
        pmt.actual_data = origindata
    }
    const status = data.data.phone_month_total.status
    const content = [
        ['全球生产量',pmt.actual_data.global_production&&pmt.actual_data.global_production.num,''],
        ['全球出库量',pmt.actual_data.global_outbound&&pmt.actual_data.global_outbound.num,''],
        ['国内出库量',pmt.actual_data.dome_outbound&&pmt.actual_data.dome_outbound.num,''],
        ['海外出库量',pmt.actual_data.overseas_outbound&&pmt.actual_data.overseas_outbound.num,''],
        ['印尼机头出库',pmt.actual_data.global_outbound&&pmt.actual_data.global_outbound.indonesia,''],
        ['印度海外出库',pmt.actual_data.overseas_outbound&&pmt.actual_data.overseas_outbound.india,''],
        ['印尼海外出库',pmt.actual_data.overseas_outbound&&pmt.actual_data.overseas_outbound.indonesia,''],
    ]
    const table2 = {
        origin:data.data.phone_month_total,
        thead,
        content,
        status,
        mykey:['标题','num','注释'],
        submitChange(row,index,mykey,newData){
            if(row2Key[row]==='indonesia1'){
                table2.origin.actual_data.global_outbound.indonesia = Number(newData)
            }else if(row2Key[row]==='indonesia2'){
                table2.origin.actual_data.overseas_outbound.indonesia = Number(newData)
            }else if (row2Key[row]==='india'){
                table2.origin.actual_data.overseas_outbound.india = Number(newData)
            }else{
                table2.origin.actual_data[row2Key[row]][index2Key[index]] = newData
            }
            
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table2.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data
            }
            sendSave(data,func)
        }
    }
    return table2

}
export default table2


