import React, { Component } from "react";
import '../css/style.css'
import line from '../img/line.png'

export default function domeTrend(title,content){
    let axis = window.previewTable[7].content[1][0]
    let colors = window.previewTable[7].content[2][0]
    let min = axis[1]?{min:axis[1]}:{}
    let max = axis[2]?{max:axis[2]}:{}
    let tickInterval = axis[3]?{tickInterval:axis[3]}:{}
    setTimeout(()=>{

       //国内产销趋势
window.$('#productionMarketing').highcharts({
    chart: {
        zoomType: 'xy',
        backgroundColor:'transparent',
        options3d: {
            enabled: true,
            alpha: 15,
            beta: 0,
            viewDistance: 25,
            depth: 100,
        },
    },
    title: {text: null},
    credits:{enabled:false},
    xAxis: [{
        categories: [],
        gridLineWidth:0,
        minPadding:10,
        labels:{
            enabled: false,
        },
    }],
    yAxis: [{ // Primary yAxis
        ...min,
        ...max,
        ...tickInterval,
        labels: {
            style: {
                color: '#fff',
                fontSize: '23px',
            }
        },
        gridLineColor:'rgba(153,153,153,.3)',
        gridLineWidth: 1,
        title: {
            enabled:false
        }
    }, { // Secondary yAxis
        title: {
            enabled:false
        },
        labels: {
            enabled:false
        },
    }],
    legend: {
        verticalAlign: 'top',
        itemDistance:30,
        itemStyle:{
            color: '#fff',
            fontSize: '25px',
        },
        margin:100,
        symbolPadding: 20,
        symbolWidth: 30
    },
    plotOptions:{
        column: {
            borderRadius: 100
        }
    },
    tooltip:{
        enabled:false
    },
    series: [{
        name: '生产',
        type: 'column',
        color:'rgba(201,166,152,.8)',
        data: content.map(v=>Number(v[1]))
    }, {
        name: '出库',
        type: 'column',
        color:'rgba(137,173,182,.8)',
        data: content.map(v=>Number(v[2]))
    },{
        name: '激活',
        type: 'spline',
        color:'rgba(255,255,255,1)',
        data: content.map(v=>Number(v[3]))
    }]
});
    })
    return (
        <div className="container">
        <div className="activationTrends">
            <div className="title-wrap">
                <p className="production-title">国内产销趋势</p>
                <img src={line} alt="line" className="production-bg" />
            </div>
            <div id="productionMarketing"></div>
            <table className="market-table">
                <thead>
                    <tr id="marketTable">
                    <td style={{backgroundColor:'rgba(73, 66, 86, 0.8)'}}>月份</td>
                    {content.map((v,index)=>{
                        return <td key={index}>{v[0].slice(4)}月</td>
                    })}
                    </tr>
                </thead>
                <tbody>
                        <tr>
                            <td className="first-td">
                                <span className="circle"></span>
                            </td>
                {content.map((item,index)=>{
                    return (
                            <td key={index}>
                            {item[1]}
                            </td>
                    )})}
                    </tr>
                        <tr>
                            <td className="first-td">
                                <span className="circle color-b"></span>
                            </td>
                
                {content.map((item,index)=>{
                    return (
                            <td key={index}>
                            {item[2]}
                            </td>
                    )})}
                    </tr>
                        <tr>
                            <td className="first-td">
                                <span className="circle color-w"></span>
                            </td>
                
                {content.map((item,index)=>{
                    return (
                            <td key={index}>
                            {item[3]}
                            </td>
                    )})}
                         </tr>

                </tbody>
            </table>
            <p className="comments">单位&nbsp;&nbsp;&nbsp;/&nbsp;&nbsp;&nbsp;万台</p>
        </div>
        </div>
    )
}


