import React, { Component } from "react";
import '../css/style.css'
import line from '../img/line.png'
const index2Num = ['one','two province','three']
export default function domeActive(title,content){
    return (
        <div className="container">
        <div className='models'>
                <div className="title-wrap">
                    <p className="production-title">国内激活（分区域）Top20</p>
                    <img src={line} alt="line" className="production-bg" />
                </div>
                <div className="models-item-content">
                    <table>
                        <thead>
                        <tr className="t-title">
                        {title.map((t,index)=>{
                            return <td key={index}>{t}</td>
                        })}
                        </tr>
                        </thead>
                    <tbody>
                        {content.map((c,i)=>{
                            i = i+1
                            return (
                                <tr key={i}>
                                 {c.map((single,index,cc)=>{
                                     console.log(typeof single)
                                    return <td key={index} className={index2Num[index]}>{index==cc.length-1?single.toFixed(1):single}</td>
                                })}
                                </tr>
                            )
                        })}
                    </tbody>
                    </table>
                    <p className="comments">单位&nbsp;&nbsp;&nbsp;/&nbsp;&nbsp;&nbsp;万台</p>
                </div>
            </div>
        </div>
    )
}


