import React from 'react'
import '../css/style.css'
import shape from '../img/Shape.png'

export default function previewTitle(month,content){
    const img = '/images/' + window.month + 'month.png'
    return (
        <div className="container">
        <div className="title" id="title">
            <img src={img} alt="标题" className="title-img" />
        </div>
        <img src="/images/Shape.png" alt="logo" className="logo" />
        <p className="describe">{content}</p>
        </div>
    )
}


