import { sendSave } from './fetchdata'
const thead = [
    ['机型','生产量','生产达成率'],
    ['坐标轴','最小值','最大值','间隔单位'],
    ['颜色','1','2','3']
]
const mykey = [
    ['type','production', 'finish_rate' ],
    ['','min','max','gap'],
    ['','color1','color2','color3']
]
let axis = [
    ['左（主要）','','',''],
    ['右（辅助）','','','']
]
const colors = [
    ['注释内容','','','']
]
export function table9(data){
    const maintain = []
    if(config_data){
        axis = JSON.parse(window.HTMLDecode(config_data)).axis
    }
    const content = [
            maintain
        ,
            axis
        ,
            colors
        ,
    ]
    const dome_production_finish = data.data.dome_production_finish
    let { actual_data, status, config_data } = dome_production_finish
    if(!actual_data){
        data.data.dome_production_finish.actual_data = []
        actual_data = data.data.dome_production_finish.actual_data
    }
    for(let key in actual_data.production){
        maintain.push(
            [ key, actual_data.production[key] , actual_data.finish_rate[key] ]
        )
    }

    const table9 = {
        thead: thead,
        content: content,
        origin: dome_production_finish,
        status,
        mykey:mykey,
        submitChange(row,index,mykey,newData){
            //获取机型
            let type = Object.keys(table9.origin.actual_data.production)[row]

            if(['min','max','gap'].indexOf(mykey)>-1){
                mykey==='min'&&(content[1][row][1] = newData)
                mykey==='max'&&(content[1][row][2] = newData)
                mykey==='gap'&&(content[1][row][3] = newData)
                return
            }
            if(['color1','color2','color3'].indexOf(mykey)>-1){
                mykey==='color1'&&(content[2][0][1] = newData)
                mykey==='color2'&&(content[2][0][2] = newData)
                mykey==='color3'&&(content[2][0][3] = newData)
                return
            }
            if(mykey === 'type'){
                for(let key in table9.origin.actual_data){
                    table9.origin.actual_data[key][newData] = table9.origin.actual_data[key][type]
                    table9.origin.actual_data[key][type] = null
                }
            }else{
                table9.origin.actual_data[mykey][type] = newData
            }
        },
        save(month,func){
            const { modules_id, modules, actual_data, status } = table9.origin
            let data = {
                month,
                modules_id,
                modules,
                status,
                data: actual_data,
                config_data: {
                    axis,
                    colors,
                }
            }
            sendSave(data,func)
        }
    }
    return table9

}

export default table9


